* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

main {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-self: center;
  padding: 30px;
}

.navbar {
  position: relative;
  background: linear-gradient(to right, #43c6ac, #191654);

  padding: 30px 30px;
}

.logo_container {
  display: flex;
  font-size: 20px;
  color: #fff;
  margin-left: 140px;
}

.logo_img {
  position: absolute;
  width: 200px;
  top: 10px;
  left: 20px;
}

.form {
  display: flex;
  flex-direction: column;
}

.form_inputs {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  display: flex;
  gap: 20px;
  margin: 30px 0;
}

.form_input {
  flex-basis: 50%;
  padding: 20px;
  border: 1px solid #43c6ac;
  border-radius: 5px;
}

.btn {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #fff;
  font-size: 16px;
  width: 100%;
  padding: 20px;
  border: none;
  border-radius: 5px;
  background: linear-gradient(to right, #43c6ac, #191654);
  cursor: pointer;
}

.memeImg_container {
  margin: auto;
  position: relative;
}

.meme_img {
  width: 100%;
}

.meme_text-top {
  font-size: 28px;
  color: #fff;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.meme_text-bottom {
  font-size: 28px;
  color: #fff;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
